* {
    font-family: 'Spectral', serif;
   }

.home {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}

.left-imgs {
    margin-top: 10vh;
    height: 100%;
    display: flex;
    justify-content: right;
}

.left-content {
    display: flex;
    align-items: center;
    text-align: left;
    margin-left: 30%;
    margin-right: 30%;
}

@media (max-width: 900px) {
    .left-content {
        margin-top: 10vh;
        height: 100%;
        width: 75vw;
    }
    .left-intro {
        width: 100%;
    }
    .left-title {
        font-size: 15px !important;
    }
    .p1, .p2 {
        font-size: 10px !important;
    }
    .left-desc {
        font-size: 11px !important;
    }
    .right-content {
        margin-bottom: 20vh !important;
    }
    .right-nav {
        text-align: center !important;
        font-size: 11px !important;
        margin-bottom: 10vh;
    }
    .img-profile {
        height: 90px !important;
        width: auto;
        padding: 0 !important;
        margin-left: 10px;
    }
    .img-city {
        height: 60px !important;
        margin-top: 40px !important;
        padding: 0 !important;
        width: auto;
    }
    .left-imgs {
        margin-top: 0;
        height: 100%;
        display: flex;
        justify-content: right;
    }
    
}

.left-title {
    font-size: 20px;
    padding-top: 10px;
    padding-bottom: 20px;
    padding-left: 0;
    margin: 0;
}

.left-intro {
    padding-left: 5%;
}

.p1, .p2 {
    padding: 0;
    margin: 0;
    font-size: 11px;
}

.p2 {
    padding-left: 15px;
}

.p-start {
    color: rgb(112, 112, 112);
}

.left-links {
    padding-top: 30px;
    padding-bottom: 30px;
    display: block;
}

.link {
    margin: 0;
    padding: 0;
}

.left-logo:hover {
    opacity: 100%;
}

.left-logo {
    height: 14px;
    width: auto;
    margin: 5px;
    padding: 0;
    opacity: 75%;
}

.left-desc {
    font-size: 12px;
}

.left-list {
    margin-left: 40px;
    list-style: 10px;
}

a {
    color: black;
    text-decoration: none;
}

a:hover {
    color: black;
}

.text-underline {
    background: linear-gradient(120deg, #e9e1d7 0%, #e9e1d7 100%);
    background-repeat: no-repeat;
    background-size: 100% 50%;
    background-position: 0 60%;
}

.right-nav {
    margin-top: 10px;
    padding: 0;
    text-align: right;
    font-size: 13px;
    color:rgb(112, 112, 112);
    margin-bottom: 10vh;
}


.img-profile {
    height: 170px;
    width: auto;
}

.img-city {
    height: 120px;
    margin-top: 70px;
    width: auto;
}

.link:hover {
    opacity: 70%;
}